import Nanobus from "nanobus";
export default class Sidebox {
    constructor(el) {
        this.el = el;
        this.events = new Nanobus();
        this.Form = new NewLayoutForm(el.querySelector('.new'));
        this.initEvents();
    }
    onOrdered(cb) {
        this.events.addListener('ordered', cb);
    }
    getListScreen() {
        return this.el.dataset.type;
    }
    initEvents() {
        const button = this.getButton();
        if (button) {
            button.addEventListener('click', e => {
                e.preventDefault();
                if (button.classList.contains('open')) {
                    this.cancelNewLayout();
                }
                else {
                    this.addNewLayout();
                }
            });
        }
        jQuery(this.el).find('.layouts__items').sortable({
            items: '.layouts__item',
            axis: 'y',
            containment: jQuery(this.el).find('.layouts__items'),
            handle: '.cpacicon-move',
            stop: () => {
                this.setNewOrder();
            }
        });
    }
    setNewOrder() {
        this.storeLayoutOrder(this.getOrder()).done(() => {
            this.events.emit('ordered', this.getOrder());
        });
    }
    getOrder() {
        let order = [];
        this.el.querySelectorAll('.layouts__item').forEach(layout => {
            order.push(layout.dataset.screen);
        });
        return order;
    }
    storeLayoutOrder(order) {
        return jQuery.ajax({
            url: ajaxurl,
            method: 'POST',
            data: {
                _ajax_nonce: AC._ajax_nonce,
                action: 'acp_update_layout_order',
                list_screen: this.getListScreen(),
                order: order
            }
        });
    }
    getButton() {
        return this.el.querySelector('a.add-new');
    }
    addNewLayout() {
        this.getButton().classList.add('open');
        this.Form.open();
    }
    cancelNewLayout() {
        this.getButton().classList.remove('open');
        this.Form.close();
    }
}
class NewLayoutForm {
    constructor(el) {
        this.el = el;
        this.initEvents();
    }
    open() {
        jQuery(this.el).slideDown();
    }
    close() {
        jQuery(this.el).slideUp();
    }
    initEvents() {
        // Add new error message
        this.el.querySelector('.new form').addEventListener('submit', e => {
            const name = this.el.querySelector('.row.name input').value.trim();
            if (!name) {
                e.preventDefault();
                this.el.querySelector('.row.name').classList.add('save-error');
            }
        });
    }
}
