import Sorting from "../layout-settings/sorting";
import Roles from "../layout-settings/roles";
import Users from "../layout-settings/users";
import HideOnScreen from "../layout-settings/hide-on-screen";
export default class LayoutSettings {
    constructor(el, form) {
        this.element = el;
        this.form = form;
        this.settings = {};
        this.init();
    }
    addSetting(name, setting) {
        this.settings[name] = setting;
    }
    init() {
        this.addSetting('sorting', new Sorting(this.form));
        this.addSetting('roles', new Roles(this.element));
        this.addSetting('users', new Users(this.element));
        this.element.querySelectorAll('#hide-on-screen').forEach(el => this.addSetting('hideonscreen', new HideOnScreen(el)));
    }
}
