const stripHtml = (content) => {
    return content ? content.replace(/(<([^>]+)>)/gi, "") : '';
};
export default class Sorting {
    constructor(form) {
        this.form = form;
        this.columns = form.getColumns();
        this.setting = form.getElement().querySelector('[data-setting="sorting-preference"]');
        this.selectElement = this.setting ? this.setting.querySelector('[name="sorting"]') : null;
        if (this.setting) {
            this.init();
        }
    }
    init() {
        this.columns.forEach(column => {
            if (this.isSortable(column)) {
                let option = document.createElement('option');
                option.value = column.getName();
                option.text = stripHtml(column.getJson().label);
                if (!option.text) {
                    let _el = column.getElement().querySelector('.column_type .inner');
                    if (_el) {
                        option.text = _el.innerText;
                    }
                }
                this.selectElement.appendChild(option);
            }
        });
        let current = this.selectElement.dataset.sorting;
        this.selectElement.querySelectorAll(`[value="${current}"]`).forEach(e => e.selected = true);
    }
    isSortable(column) {
        return column.getElement().querySelectorAll('table[data-setting="sort"] input[value="on"]:checked').length > 0;
    }
}
